	                    _paragraph2.default,
	                    null,
	                    'The graphics in NSF had one major revamp since the game started four months ago. In first versions is was all white and black, with white being the primary color and black being the color of the elements like platforms, level borders and the player. After the revamp the game got its current look, featuring the dark blue background, yellow and green glowing elements and UI, plus some other, less represented colors. In future this might change as well, but for now I\u2019m OK with this appearance.'
